let UIManager = require("UIManager");
const { Furwee } = require("Furwee");

let SpeechTool = {};

SpeechTool.ui = null;
SpeechTool.useWebRecognition = false;
SpeechTool.recognition = null;
SpeechTool.textRecognition = "";

 //api
 SpeechTool.stream = null;
 SpeechTool.recorder = null;
 
 SpeechTool.gumStream = null;
 //stream from getUserMedia() 
 SpeechTool.rec = null;
 //Recorder.js object 
 SpeechTool.input = null;
 SpeechTool.audioContext = null;

 SpeechTool.isWorkingChecked = false;

 SpeechTool.initRecognition = function(uibase){
    UIManager.uiBase = uibase;

    window.SpeechRecognition = window.SpeechRecognition
    || window.webkitSpeechRecognition;

    SpeechTool.useWebRecognition = SpeechRecognition != undefined;
    UIManager.instance.debugNode.string += "useWebRecognition:" +  SpeechTool.useWebRecognition + "\n";

    SpeechTool.textRecognition = "";
},

SpeechTool.isWorking = function(){
    if(!SpeechTool.recognition){
        return true;
    }

    if(!SpeechTool.isWorkingChecked){
        SpeechTool.isWorkingChecked = true;

        UIManager.instance.debugNode.string += "isWorking "+ SpeechTool.recognition.audiostart +"\n" +SpeechTool.recognition.audioend +"\n";

        if((!SpeechTool.recognition.audiostart)
            &&(!SpeechTool.recognition.audioend)
            &&(!SpeechTool.recognition.end)){

                SpeechTool.useWebRecognition = false;


                SpeechTool.recognition.onerror = null;
                SpeechTool.recognition.onresult = null;
                SpeechTool.recognition.onaudiostart = null;
                SpeechTool.recognition.onaaudioend = null;
                SpeechTool.recognition.onend = null;


                return false;
            }
    }

    return true;
},


SpeechTool.startWebRecognition = function(){
    let finalString = "";
    // check if work properly
    if(SpeechTool.recognition){
        SpeechTool.recognition.onerror = null;
        SpeechTool.recognition.onresult = null;
        SpeechTool.recognition.onaudiostart = null;
        SpeechTool.recognition.onaaudioend = null;
        SpeechTool.recognition.onend = null;
    }

    //
    SpeechTool.recognition = new SpeechRecognition();
    SpeechTool.recognition.interimResults = true;
    SpeechTool.recognition.continuous = true;
    SpeechTool.recognition.lang = Furwee.LANG ;

    UIManager.instance.debugNode.string += "startWebRecognition 1\n";
   
    SpeechTool.recognition.onresult = function (event) {

        console.log(event);
        UIManager.instance.debugNode.string += "startWebRecognition 3"+event+"\n";
        let interString = "";
        let autoEnd = false;
        for (let i = event.resultIndex; i < event.results.length; ++i) {
            // If the result item is Final, add it to Final Transcript, Else add it to Interim transcript
            if (event.results[i].isFinal) {
                finalString += event.results[i][0].transcript;
                SpeechTool.textRecognition = finalString;
                autoEnd = true;
            
            }
            else{
                interString += event.results[i][0].transcript;
                SpeechTool.textRecognition = finalString + interString;
            
            }
        }
        //////////
        UIManager.uiBase.text.string = SpeechTool.textRecognition;

        if (UIManager.uiBase.text.enabled) {
            UIManager.uiBase.updateUIAfterRecord();
        }
        UIManager.instance.debugNode.string += "startWebRecognition 2\n";

        if(autoEnd){
            UIManager.uiBase.stopRecord();
            if(UIManager.uiBase.text.string.length != 0){
                UIManager.instance.game.sendHandler()
            }
        }
    };

    SpeechTool.recognition.onerror = function(event) {

        UIManager.instance.debugNode.string += "startWebRecognition error"+event.error+"\n" + event.message+"\n";
        if(event.error == "network"){
            SpeechTool.useWebRecognition = false;
        }
    }

    // testing
    SpeechTool.recognition.onaudiostart = function(evt){
        UIManager.instance.debugNode.string += "onaudiostart " + evt + "\n";
        evt.target.audiostart = true;
    }

    SpeechTool.recognition.onaudioend = function(evt){
        UIManager.instance.debugNode.string += "onaudioend " + evt + "\n";
        evt.target.audioend = true;
    }

    SpeechTool.recognition.onend = function(evt){
        UIManager.instance.debugNode.string += "onend " + evt + "\n";
        evt.target.end = true;
    }

    /*

    SpeechTool.recognition.onnomatch = function(evt){
        UIManager.instance.debugNode.string += "onnomatch " + evt + "\n";
        
    }

    SpeechTool.recognition.onsoundstart = function(evt){
        UIManager.instance.debugNode.string += "soundstart " + evt + "\n";
        
    }

    SpeechTool.recognition.onsoundend = function(evt){
        UIManager.instance.debugNode.string += "soundend " + evt + "\n";
        
    }

    SpeechTool.recognition.onspeechstart = function(evt){
        UIManager.instance.debugNode.string += "speechstart " + evt + "\n";
        
    }

    SpeechTool.recognition.onspeechend= function(evt){
        UIManager.instance.debugNode.string += "speechend " + evt + "\n";
        
    }

    SpeechTool.recognition.onstart= function(evt){
        UIManager.instance.debugNode.string += "start " + evt + "\n";
        
    }
    */
    //test end


    SpeechTool.recognition.start();
    UIManager.instance.debugNode.string += "startWebRecognition 6\n";
},

SpeechTool.stopWebRecognition = function(){
    SpeechTool.recognition.stop();
},


//start api recognition
SpeechTool.startAPIRecognition= function(){
    
    var AudioContext = window.AudioContext || window.webkitAudioContext;

    let constraints  =  {
        audio: true,
        video: false
    } ;

    navigator.mediaDevices.getUserMedia(constraints).then(function(stream) {

        SpeechTool.audioContext = new AudioContext();

        console.log("getUserMedia() success, stream created, initializing Recorder.js ..."); 
        /* assign to gumStream for later use */
        SpeechTool.gumStream = stream;
        /* use the stream */
        SpeechTool.input = SpeechTool.audioContext.createMediaStreamSource(stream);
        /* Create the Recorder object and configure to record mono sound (1 channel) Recording 2 channels will double the file size */
        SpeechTool.rec = new Recorder(SpeechTool.input, {
            numChannels: 1
        }) 
        //start the recording process 
        SpeechTool.rec.record()
        console.log("Recording started");
    }).catch(function(err) {
        
    });
},

SpeechTool.stopAPIRecognition = function(){

    SpeechTool.rec.stop(); //stop microphone access 
    SpeechTool.gumStream.getAudioTracks()[0].stop();
    //create the wav blob and pass it on to createDownloadLink 

    SpeechTool.rec.exportWAV(function(blob) {

        let xhr = new XMLHttpRequest();
        let requestURL = UIManager.instance.game.URL + "/audio-to-text/" + UIManager.instance.game.URL_SUFFIX;
        xhr.onreadystatechange = function () {
            if (xhr.readyState == XMLHttpRequest.DONE) {
                if (xhr.status == 200) {
                    let json = JSON.parse(xhr.responseText);
                    if(json.text != null){
                        /////////////
                        UIManager.uiBase.text.string = json.text;
                        UIManager.uiBase.updateUIAfterRecord();
                    }
                }
            }
        }
        //let params = JSON.stringify({ "audio": blob});
        xhr.open('POST', requestURL, true);

        //URL = window.URL || window.webkitURL;
        
        var fd=new FormData();
        fd.append("audio",blob);

        xhr.send(fd);
    });


}


export{SpeechTool};


