let MusicToggle = require("MusicToggle");
let UIManager = require("UIManager");
let LayerCamerasMove = require("LayerCamerasMove");
const { Furwee } = require("Furwee");
let Pet = require("Pet");
const { Server } = require("../tool/Server");

let Game = cc.Class({
    extends: cc.Component,

    properties: {

        audioID: -1,

        historyObjects: [],

        pet: Pet,


        balloonPrefab: cc.Prefab,
        balloonNode: cc.Node,

        introSound: {
            type: cc.AudioClip,
            default: null
        },

        initialMsgJSON: null,

        furweeIntialized: false,

        enableTextInput: false,

        uiManager: UIManager,
        cameraMove: LayerCamerasMove,
        music: MusicToggle,
        //ui related
        editBox: cc.EditBox,
        sendButton: cc.Node,


        nextTalks: []


    },

    start() {
    },

    onLoad() {


        this.uiManager.game = this;
        this.cameraMove.game = this;
        this.music.game = this;

        this.setEnableTextInput(false);
        this.onTextLenChange(this.editBox.string);
        this.startFurwee();
    },

    handleConnect() {
        console.log('connected', this.socket.id);
    },

    startFurwee() {
        this.pet.play();

        Server.getInitialMessage(function(responseText){
            this.initialMsgJSON = JSON.parse(responseText);
            if (!this.tryToStartFurweeIntro()) {
                //preload sound
                let remoteUrl = this.initialMsgJSON.audio_file_link;
                cc.loader.load({ url: remoteUrl, type: 'wav' });
            }

        }.bind(this))
    },

    tryToStartFurweeIntro() {
        if (this.uiManager.isBlockActive()) {
            return false;
        }
        if (!this.initialMsgJSON) {
            return false;
        }

        if (this.furweeIntialized) {
            return false;
        }

        this.furweeIntialized = true;
        //this.pet.playWelcome();
        this.setEnableTextInput(true);
        //this.pet.playIntro();
        this.onTTSCompleted(this.initialMsgJSON, this.initialMsgJSON.reply);

        this.historyObjects.push({ "index": this.historyObjects.length, "reply": this.initialMsgJSON.reply, "message": this.initialMsgJSON.message });
        return true;
    },


    onTTSCompleted(info, balloonText) {
        if (!info) {
            return;
        }

       

        let remoteUrl = info.audio_file_link;

        let balloon = this.addBallon(balloonText, true).getComponent("Ballon");
        if(this.audioID != -1) {
            balloon.whiteBG.opacity = 128;
            balloon.text.node.opacity = 128;
        }
                
        cc.loader.load({ url: remoteUrl, type: 'wav' }, function (err, res) {
            // Use texture to create sprite frame
            this._startPlayTalking(res, balloon, info.lip_sync_animation);
        }.bind(this));
    },

    _startTalk(res, info) {
        this.audioID = cc.audioEngine.play(res);
        if (this.audioID && this.music.shouldbeMuted()) {
            cc.audioEngine.setVolume(this.audioID, 0);
        }
        this.audioOffset = 0;
        this.audioInfo= info;
        this.updateMouth();


        cc.audioEngine.setFinishCallback(this.audioID, this._completeTalk.bind(this));
    },

    _completeTalk() {
        this.audioID = -1;
        this.pet.startIdle();

        if (this.nextTalks.length) {
            let nextTalk = this.nextTalks.splice(0, 1)[0];
            nextTalk.balloonComp.whiteBG.opacity = 255;
            nextTalk.balloonComp.text.node.opacity = 255;
            this._startTalk(nextTalk.audioRes, nextTalk.info);
        }
    },

    _startPlayTalking(res, balloon, audioInfo) {
        if (this.pet.isReadyToTalk()) {
            if (this.audioID != -1) {
                this.nextTalks.push({ balloonComp: balloon, audioRes: res, info: audioInfo });
            }
            else {
                this._startTalk(res, audioInfo);
            }
        }
        else {
            //this.pet.playIntro();
            setTimeout(() => {
                this._startPlayTalking(res, balloon, audioInfo);
            }, 16);
        }

    },

    didReturnHandler() {
        this.sendHandler();
        setTimeout(function () {
            this.editBox.focus();
        }.bind(this), 0);
    },


    sendHandler() {
        if (!this.enableTextInput) {
            return;
        }
        let sendText = this.editBox.string;
        if (sendText.trim().length == 0) {
            return;
        }

        this.pet.resetIdle();

        /* this.socket.emit("tts", sendText);
         return;
    */

        this.setEnableTextInput(false);
        this.addBallon(sendText, false);
        this.editBox.string = "";
        if (!cc.sys.isMobile) {
            this.editBox.focus();
        } else {
            this.editBox.blur();
        }

        this.onTextLenChange(this.editBox.string);

        Server.getMessage(sendText, this.historyObjects, function(responseText){
            let json = JSON.parse(responseText);
            this.setEnableTextInput(true);
            this.onTTSCompleted(json, json.reply);

            this.historyObjects.push({ "index": this.historyObjects.length, "reply": json.reply, "message": json.message });
        }.bind(this));
        
        Server.getEmotion(sendText, function(responseText){
            let response = JSON.parse(responseText);
            this.pet.setEmotion(response.emotion);
        }.bind(this))

    },

    updateMouth() {
        //audio_offset_ms: 1162.5, viseme_id: 11}
        let id = this.audioInfo[this.audioOffset].viseme_id;
        this.pet.updateMouth(id);
    },

    update(dt) {
        if (this.audioID != -1) {
            let time = cc.audioEngine.getCurrentTime(this.audioID)

            while (this.audioInfo.length > this.audioOffset
                && (this.audioInfo[this.audioOffset].audio_offset_ms < time * 1000 + 15)) {
                // && ((this.audioOffset + 1 >= this.audioInfo.length) || (Math.abs(this.audioInfo[this.audioOffset ].audioOffset - time* 1000 ) > (this.audioInfo[this.audioOffset +1].audioOffset - time* 1000 )))){
                this.updateMouth();
                this.audioOffset += 1;

            }
        }
    },

    onTextLenChange(textContent) {
        this.sendButton.active = textContent.length != 0;
        this.uiManager.updateSendButton(this.sendButton.active);
    },

    addBallon(message, isFurwee) {
        for (var i = 0; i < this.balloonNode.children.length; ++i) {
            this.balloonNode.children[i].getComponent("Ballon").step();
        }

        let balloon = cc.instantiate(this.balloonPrefab);
        balloon.getComponent('Ballon').init(message, isFurwee);
        balloon.opacity = 0;
        this.balloonNode.addChild(balloon);
        return balloon;

        // this.balloonNode.parent.parent.getComponent(cc.ScrollView).stopAutoScroll();
    },

    blockClickHandler() {
        this.music.initialize();
        this.tryToStartFurweeIntro();
        this.pet.playWelcome();
    },

    errorClickHandler() {
        if (window.captureError) {
            window.captureError();
        }

    },

    setEnableTextInput(bool) {
        this.enableTextInput = bool;
    }
});
