let UIManager = require("UIManager");

let Pet = cc.Class({
    extends: cc.Component,


    properties: {
        headAnim: cc.Animation,
        bodyAnim: cc.Animation,
        mouthNode: cc.Node,
        idleMouthTimeout: -1,
        state: null,
        emotion: "neutral",
        tummy: cc.Node,
        heartPrefab: cc.Prefab,

        _heart: cc.Node,

        laughSound: {
            type: cc.AudioClip,
            default: null
        },

        isAnimLaughLoaded: false,
        isAnimIdleWaitLoaded: false
    },


    onLoad() {
        this.tummy.on(cc.Node.EventType.TOUCH_MOVE, this._onTummyHandler, this);
        this.tummy.on(cc.Node.EventType.MOUSE_MOVE, this._onTummyHandler, this);

        this._randTalkAnims = ["talking_l_a"]
        this._initPetResourceLoading();
    },

    onDestroy() {
        this.tummy.off(cc.Node.EventType.TOUCH_MOVE, this._onTummyHandler, this);
        this.tummy.off(cc.Node.EventType.MOUSE_MOVE, this._onTummyHandler, this);
    },

    play() {
        this._playFurweeAnim("happyidle");
        this.state = "intro_idle";
    },

    playWelcome() {
        this._playFurweeAnim("intro");
        this._setAnimFinishHandler();
        this.state = "intro_jump";
    },

    playIntro() {
        /*
        this._playFurweeAnim("happywave");
        this._setAnimFinishHandler();
        this.state = "wave_1";
        */
    },

    updateMouth(id) {
        this._stopIdleMouth();
        if (this.state.startsWith("intro") || this.state.startsWith("wave")) {
            // skip
            if(this.state == "wave_waiting"){
                this._playFurweeAnim("happywave");
                this._setAnimFinishHandler();
                this.state = "wave_1";
            }
        }
        else if (this.state != "speak") {
            this.state = "speak";
            switch (this.emotion) {
                case "positive":
                    this._playFurweeAnim("tallking_2_hands");
                    break;
                case "negative":
                    this._playFurweeAnim("sadidle");
                    break;
                default://"talking_r_a", "talking_l_b","talking_l_c", "talking_r_b", "talking_r_c"
                    //let randArray = ["talking_l_a"];
                    let act = Math.floor(Math.random() * this._randTalkAnims.length);
                    this._playFurweeAnim( this._randTalkAnims[act]);
            }
        }

        let node = this.mouthNode.getChildByName("mouth_" + id);
        if (node) {
            this._clearMouth();
            node.active = true;
        }
    },

    startIdle() {
        this.state = "idle";
        this._stopIdleMouth();

        this._clearMouth();
        //
        this._setIdleMouth();


        switch (this.emotion) {
            case "negative":
                this._playFurweeAnim("sadidle");
                break;
            case "positive":
                this._playFurweeAnim("happyidle");
                break;
            default:
                this._playFurweeAnim("normalidle");
        }
    },

    resetIdle(){
        if(!this.isAnimIdleWaitLoaded){
            return;
        }
        if(this.idleTimer){
            clearTimeout(this.idleTimer);
        }
        this.idleTimer = setTimeout(() => {
            this._playFurweeAnim("listenwaitidle");
        }, 30000);
    },

    _setIdleMouth() {
        let idle = Math.floor(Math.random() * 2);
        this.mouthNode.getChildByName("idle_" + idle).active = true;
        this.idleMouthTimeout = setTimeout(() => {
            this._setIdleMouth();
        }, 2000);
    },

    _cancelAnimHandler(){
        this.bodyAnim.off("lastframe", this._animComplete.bind(this));
        this.bodyAnim.off("finished", this._animComplete.bind(this));
    },

    _setAnimLastFrameHandler(){
        this.bodyAnim.off("lastframe", this._animComplete.bind(this));
        this.bodyAnim.on("lastframe", this._animComplete.bind(this));
    },

    _setAnimFinishHandler() {
        this.bodyAnim.off("finished", this._animComplete.bind(this));
        this.bodyAnim.on("finished", this._animComplete.bind(this));
    },

    _animComplete() {
        console.log('_animComplete');
        this.bodyAnim.off("finished", this._animComplete.bind(this));
        this.bodyAnim.off("lastframe", this._animComplete.bind(this));
        switch (this.state) {
            case "intro_jump":
                this._playFurweeAnim("normalidle");
                this.state = "wave_waiting";
                break;
            case "wave_1":
                this._playFurweeAnim("talking_l_a");
                this.state = "speak";
                this.resetIdle();
                break;
            default:
                this._playFurweeAnim("normalidle");
                this.state = "idle";
                break;
        }
       
    },

    _clearMouth() {
        for (let i = 0; i <= 21; i++) {
            let node2 = this.mouthNode.getChildByName("mouth_" + i);
            node2.active = false;
        }

        for (let i = 0; i < 2; i++) {
            let node2 = this.mouthNode.getChildByName("idle_" + i);
            node2.active = false;
        }

        let node3 = this.mouthNode.getChildByName("open_mouth");
        node3.active = false;
    },



    _stopIdleMouth() {
        clearTimeout(this.idleMouthTimeout);
        this.idleMouthTimeout = -1;
    },


    _playFurweeAnim(anim) {
        console.log("playAnim", anim);
        this._cancelAnimHandler();

        this.headAnim.play(anim + "_head");
        this.bodyAnim.play(anim + "_body");

        if (anim == "laugh") {
            if (!this._heart) {
                this._heart = cc.instantiate(this.heartPrefab);
                this._heart.y = this.tummy.y;
                this.node.addChild(this._heart);

                
            }
            else {
                this._heart.getComponent(cc.Animation).play();
            }

            if(! UIManager.instance.game.music.shouldbeMuted()){
                cc.audioEngine.play(this.laughSound);

            }
        }
    },

    setEmotion(state) {
        console.log("setemotion", state);
        this.emotion = state;
    },

    isReadyToTalk() {
        return !this.state.startsWith("intro");
    },

    _onTummyHandler() {
        if(!this.isAnimLaughLoaded){
            return;
        }
        if (this.state == "idle") {
            this.state = "laugh";
            this._playFurweeAnim("laugh");
            this._setAnimFinishHandler();
        }
    },


    ///pet resources loading
    _initPetResourceLoading(){
        this._initLaugh();
    },

    _initLaugh(){
        cc.loader.loadRes('pet_resources_laugh', function (err, prefab) {
            if (err) {
                cc.error(err.message || err);
                return;
            }
            this._addAnimClips(prefab.data);

            this.isAnimLaughLoaded = true;
            this._initIdleWait();
        }.bind(this));
    },

    _initIdleWait(){
        cc.loader.loadRes('pet_resources_idlewait', function (err, prefab) {
            if (err) {
                cc.error(err.message || err);
                return;
            }
            this._addAnimClips(prefab.data);

            this.isAnimIdleWaitLoaded = true;
            this.resetIdle();
            this._initTalk();
        }.bind(this));
    },

    _initTalk(){
        cc.loader.loadRes('pet_resources_talk', function (err, prefab) {
            if (err) {
                cc.error(err.message || err);
                return;
            }
            this._addAnimClips(prefab.data);
            this._randTalkAnims = this._randTalkAnims.concat(["talking_r_a", "talking_l_b","talking_l_c", "talking_r_b", "talking_r_c"]);
            console.log(this._randTalkAnims);
        }.bind(this));
    },

    _addAnimClips(node){
        let bAnim = node.getChildByName("body").getComponent(cc.Animation);
        let hAnim = node.getChildByName("head").getComponent(cc.Animation);
        
        for(let i = 0; i < bAnim._clips.length; i++){
            this.bodyAnim.addClip( bAnim._clips[i],  bAnim._clips[i].name);
        }

        for(let i = 0; i < hAnim._clips.length; i++){
            this.headAnim.addClip( hAnim._clips[i],  hAnim._clips[i].name);
        }
    }
})