const { Furwee } = require("Furwee"); 
let Server = {};

Server.URL = "http://40.121.137.102";
Server.URL_SUFFIX = "";

Server.init = function(){

    if (!Furwee.DEBUG) {
        Server.URL = "/api";
        Server.URL_SUFFIX = "";
        Furwee.LOGOUT_URL = "/logout";
        Furwee.HOME_URL = "/";
    }
    else {
        Server.URL = "https://furwee.ai/api";
        Server.URL_SUFFIX = "";
        Furwee.LOGOUT_URL = "https://furwee.ai/logout";
        Furwee.HOME_URL = "https://furwee.ai/home";
    }
}
Server.init();

Server.getInitialMessage = function (callback){
        let xhr = new XMLHttpRequest();
        let requestURL = Server.URL + "/initial-message/" + Server.URL_SUFFIX;
        xhr.onreadystatechange = function () {
            if (xhr.readyState == XMLHttpRequest.DONE) {
                if (xhr.status == 200) {
                    callback(xhr.responseText);
                }
            }
        }
        xhr.open('GET', requestURL, true);
        xhr.setRequestHeader('Content-type', 'application/json');
        xhr.send("");
}

Server.getMessage = function (sendText, historyObjects, callback){
        let xhr = new XMLHttpRequest();

        let requestURL = Server.URL + "/messages/" + Server.URL_SUFFIX;
        xhr.onreadystatechange = function () {
            if (xhr.readyState == XMLHttpRequest.DONE) {
                if (xhr.status == 200) {
                    callback(xhr.responseText);
                }
            }
        }
        let params = JSON.stringify({ "message": sendText, history: historyObjects });
        xhr.open('POST', requestURL, true);
        xhr.setRequestHeader('Content-type', 'application/json');
        xhr.send(params);
}

Server.getEmotion = function (sendText, callbak){
        let xhr2 = new XMLHttpRequest();
        let requestURL2 = Server.URL + "/emotion/" + Server.URL_SUFFIX;
        xhr2.onreadystatechange = function () {
            if (xhr2.readyState == XMLHttpRequest.DONE) {
                if (xhr2.status == 200) {
                    callbak(xhr2.responseText);
                }
            }
        }
        let params2 = JSON.stringify({ "message": sendText });
        xhr2.open('POST', requestURL2, true);
        xhr2.setRequestHeader('Content-type', 'application/json');
        xhr2.send(params2);
}


export{Server};