let UIManager = require("UIManager");
const {SpeechTool} = require("SpeechTool");

let UIBase = cc.Class({
    extends: cc.Component,

    properties: {
        sendButton:cc.Node,
        text: cc.EditBox,
        ///recorder

        microButton: cc.Node,
        recordButton: cc.Node,

        languagePanel: cc.Node

    },
    
    onLoad(){
        this.text._impl._elem.autocomplete = "off";
    },

    //mobile
    startRecord() {
        this.microButton.active = false;
        this.recordButton.active = true;

        this.text.enabled = false;

        SpeechTool.initRecognition(this);

        UIManager.instance.game.music.onMicChange(true);
        
        if(SpeechTool.useWebRecognition && SpeechTool.isWorking()){
            SpeechTool.startWebRecognition();
        }
        else{
            SpeechTool.startAPIRecognition();
        }
    },

    stopRecord() {
        this.microButton.active = true;
        this.recordButton.active = false;

        UIManager.instance.game.music.onMicChange(false);

        if(SpeechTool.useWebRecognition){
            SpeechTool.stopWebRecognition();
        }
        else{
            SpeechTool.stopAPIRecognition();
        }
       
        this.text.enabled = true;
        this.updateUIAfterRecord();
    },

    updateUIAfterRecord() {
        this.sendButton.active = (this.text.string.length != 0);
        this.updateSendButton(this.sendButton.active);
    },

    updateSendButton(active) {
        this.microButton.active = !active;
    },

    toggleLanguagePanel(){
        this.languagePanel.active = !this.languagePanel.active;
        if( this.languagePanel.active){
            UIManager.instance.languagePanel = this.languagePanel;
            console.log("toggle",  UIManager.instance.languagePanel);
        }
        
    },

    hideLanguagePanel(){
        this.languagePanel.active = false;
        UIManager.instance.languagePanel = null;
    }
});
