const { Furwee } = require("Furwee");

let languagePanel = cc.Class({
    extends: cc.Component,

    properties:{
        toggles:[cc.Toggle],
        toggleIndex: 0
    },

    onToggleLanuge(evt, param){
        console.log(param);
        this.toggleIndex = parseInt(param);

        const langChoice = ["en","zh","de","pt","it","ko","es","fr","ja"]
        Furwee.LANG = langChoice[this.toggleIndex];
        console.log(" Furwee.LANG ",  Furwee.LANG );
    },


    setToggleIndex(index){
        this.toggleIndex = index;
        this.toggles[this.toggleIndex].check();

        
    }
})